.btn-wrapper {
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4em;
    background-color: black;
    color: white;
    flex: 1;
    outline: 1px solid #293944;
}

.btn-wrapper:active:hover {
    background-color: #EEE;
}
 .operator {
        background-color: #e79c78;
        color: whitesmoke;
        outline: 1px solid #293944;
    }
    

.operator:active:hover {
    background-color: #D3815B;
}
